//  import
import { useState, useEffect } from "react";
import axios from "axios";
import validator from "validator";

// hook import
import useInput from "../../hooks/useInput";

// CSS import
import styles from "./SignUp.module.css";

// config import
import { URL_SIGNUP } from "../../config/config";

export default function SignUp({setSignUpSuccess}){
    //   url
    const url = URL_SIGNUP;

    //     state
    const [nickName, nickNameChange] = useInput("");
    const [email, emailChange] = useInput("");
    const [pw, pwChange] = useInput("");

    //      state
    const [nickNameValid, setNickNameValid] = useState(false);
    const [emailValid, setEmailValid] = useState(false);
    const [pwValid, setPwValid] = useState(false);

    //      state
    const [notAllow, setNotAllow] = useState(true);

    //  
    let signUp = (event) => {
        event.preventDefault();
        pushData(nickName, email, pw); //  
        setSignUpSuccess(true); //    true 
    }

    let pushData = async(nickName, email, pw) => {
        try{
            //  
            // eslint-disable-next-line
            const res = await axios.post(url, {
                nickName: nickName, // 
                email: email, // 
                pw: pw, // 
            })

            return true;
        }
        //  
        catch(err){
            console.log(err) //  

            return false;
        }
    };

    //      useEffect
    useEffect(()=>{
        //      
        if(nickNameValid && emailValid && pwValid){
            setNotAllow(false);
            return;
        }
        setNotAllow(true);
    }, [nickNameValid, emailValid, pwValid])

    //      useEffect
    useEffect(()=>{
        // validator      .
        nickName.length > 0 ? setNickNameValid(true) : setNickNameValid(false);
        validator.isEmail(email) ? setEmailValid(true) : setEmailValid(false);
        validator.isStrongPassword(pw, { minLength: 6, maxLength: 20, minLowercase: 1, minUppercase: 0, minSymbols: 0, minNumbers: 1 }) ? setPwValid(true) : setPwValid(false);
    }, [nickName, email, pw])

    return (
        <form className={styles.body}>
            <div className={styles.profile}/>
            <input
                type="text"
                placeholder=""
                value={nickName}
                onChange={nickNameChange}
            />
            <input
                className={styles.inputMarin}
                type="text"
                placeholder=""
                value={email}
                onChange={emailChange}
            />
            <div className={styles.container}>
                {!emailValid && email.length > 0 && (
                    <div className={styles.errorText}>   .</div>
                )}
            </div>
            <input
                className={styles.inputMarin}
                type="text"
                placeholder=""
                value={pw}
                onChange={pwChange}
            />
            <div className={styles.container}>
                {!pwValid && pw.length > 0 && (
                <div className={styles.errorText}>,   6  20  .</div>
            )}
            </div>
            <button disabled={notAllow} className={styles.button} onClick={signUp}></button>
        </form>
    );
}