//  import
import axios from "axios";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Cookie from "js-cookie";

// config import
import { URL_LOGIN } from "../../config/config";

//  import
import useInput from "../../hooks/useInput";

// CSS import
import styles from "./Member.module.css";

export default function Member(){
    //  
    const minuteExpries = 30; // 
    const expiresTime = minuteExpries / 60 / 24; // 

    //   url
    const url = URL_LOGIN;

    //   state
    const [email, emailChange] = useInput("");
    const [pw, pwChange] = useInput("");

    //   
    const movePage = useNavigate();

    //    
    const [error, setError] = useState(false);

    // dataPost 
    let dataPost = async(email, pw) => {
        try{
            //  pw axios  url   jwt  
            const res = await axios.post(url, {
                email,
                pw,
            })

            //  
            if(res.status === 200 && res.data.length === 0){
                throw new Error("members/login axios.post   .");
            }

            Cookie.set("jwt", res.data, { expires: expiresTime, secure: true }); // jwt   

            return true;
        }
        //  
        catch(err){
            console.log(err); //   

            return false;
        }
    }

    //      
    let login = (e) => {
        e.preventDefault();
        
        dataPost(email, pw)
        .then((res) => {
            if(res === true){
                movePage("/");
            }
            else{
                setError(true); //   
            }
        });
    }
    
    return (
        <form className={styles.body}>
            <input
                type="text" 
                placeholder=""
                value={email}
                onChange={emailChange}
            />
            <input 
                className={styles.inputMarin} 
                type="text" 
                placeholder=""
                value={pw}
                onChange={pwChange}
            />
            <div className={styles.container}>
                {error && (
                <div className={styles.errorText}>   .</div>
            )}
            </div>
            <button className={styles.button} onClick={login}></button>
        </form>
    );
}