//  import
import { Link } from "react-router-dom";

// CSS import
import styles from "./LogoNav.module.css";

export default function LogoNav(){
    return (
        <div className={styles.body}>
            <Link to="/" className={styles.link}>
                <div className={styles.logoTxt}>HAMSTORY</div>
            </Link>
        </div>
    );
}