//  import
import { Link, useLocation } from 'react-router-dom';

// css import
import styles from './CommunityHeader.module.css';

export default function CommunityHeader() {
    const basePath = '/community'; //  
    const location = useLocation(); //  url location 

    //   
    const menuItems = [
        { name: '', path: basePath + '/notice' },
        { name: ' ', path: basePath + '/bestPost' },
        { name: ' ', path: basePath + '/latestPost' },
        { name: '', path: basePath + '/freeBoard' },
        { name: ' ', path: basePath + '/album' },
    ];

    return (
        <div className={styles.body}>
        {menuItems.map((item, index) => (
            item.path ? (
            <Link key={index} to={item.path} className={`${styles.link} ${location.pathname === item.path ? styles.activeLink : ''}`}>
                <div className={styles.container}>{item.name}</div>
            </Link>
            ) : (
            <div key={index} className={styles.container}>{item.name}</div>
            )
        ))}
        </div>
    );
}